"use strict";
const fs = require('fs');
const mariadb = require('mariadb');
require('dotenv').config();
const serverCert = [fs.readFileSync('aws_skysql_chain.pem')];
const { DATABASE_USERNAME, DATABASE_PASSWORD, DATABASE_NAME, DATABASE_PORT, DATABASE_HOST, } = process.env;
const getDb = () => {
    let pool;
    try {
        pool = mariadb.createPool({
            acquireTimeout: 3000,
            ssl: { ca: serverCert },
            user: DATABASE_USERNAME,
            password: DATABASE_PASSWORD,
            database: DATABASE_NAME,
            host: DATABASE_HOST,
            port: DATABASE_PORT,
        });
    }
    catch (e) {
        throw e;
    }
    return pool;
};
module.exports = getDb();
